}

.profile-me__img img {
    width: 100%;
    height: 100%;
    border-radius: 100px;
}

.about-profile__skill {
    display: flex;
    height: 30px;
    justify-content: space-between;
    background-color: #EDEDED;
    margin-bottom: 10px;

}

.profile-skill__bar {
