.header {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.header__underline {
    width: 100px;
    border: 2px solid #47494B;
    margin: 15px auto;
}

.header__title {
    color: #444649;
    font-weight: 600;
    font-size: 2.5em;
}

.header__subtitle {
    font-size: 1.5em;
    font-weight: 500;
}
.home {
    height: 100vh;
    background: url(/website/static/media/main-parallax.528d3891.jpg);
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center center;
    background-attachment: fixed;
    opacity: 0.9;
}

.home .home__header {
    width: 100%;
    max-width: 1280px;
    height: 70px;
    background: transparent;
    position: fixed;
    left: 50%;
    -webkit-transform: translate(-50%, 0);
            transform: translate(-50%, 0);
    color: white;
    display: flex;
    justify-content: space-between;
    align-items: center;

}

.home .home__header .home-header__logo {
    display: flex;
    align-items: center;
    font-size: 40px;
    cursor: pointer;
    transition: .5s;
    overflow: hidden;
}

.home .home__header .home-header__logo .subtitle {
    font-size: 17px;
    font-weight: 600;
    margin-left: 10px;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    opacity: 0;
    transition: .5s;
}

.home .home__header .home-header__logo:hover {
    opacity: .8;
}

.home .home__header .home-header__logo:hover .subtitle {
    -webkit-transform: translateX(0);
            transform: translateX(0);
    opacity: .9;

}

.home-header__nav {
    display: flex;
    align-items: center;
}

.home-header__nav .nav__menu {
    display: flex;
    margin-right: 200px;
    -webkit-transform: translateX(50%);
            transform: translateX(50%);
    opacity: 0;
    font-size: 20px;
    transition: .5s;
}

.home-header__nav .nav__menu.active {
    -webkit-transform: translateX(0);
            transform: translateX(0);
    opacity: 1;
}

.home-header__nav .nav__menu span {
    padding: 7px;
    margin-right: 13px;
    cursor: pointer;
    font-weight: 600;
    transition: color .5s;
}

.home-header__nav .nav__menu span:hover {
    color: #404040
}

.home-header__nav .nav__hamberger  {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 24px;
    height: 16px;
    position: relative;
    cursor: pointer;
}

.home-header__nav .nav__hamberger .nav__hamberger--line {
    width: 100%;
    height: 3px;
    background: #fff;
    position: absolute;
    transition: -webkit-transform .3s;
    transition: transform .3s;
    transition: transform .3s, -webkit-transform .3s;
}


.home-header__nav .nav__hamberger.active .nav__hamberger--line:nth-child(1) {
    -webkit-transform: rotate(45deg);
            transform: rotate(45deg);
    top: 8px;
}

.home-header__nav .nav__hamberger.active .nav__hamberger--line:nth-child(2) {
    background: transparent;
}

.home-header__nav .nav__hamberger.active .nav__hamberger--line:nth-child(3) {
    -webkit-transform: rotate(135deg);
            transform: rotate(135deg);
    top: 8px;

}

.home-header__nav .nav__hamberger .nav__hamberger--line:nth-child(1) {
    top: 0px;
}
.home-header__nav .nav__hamberger .nav__hamberger--line:nth-child(1) {
    top: 8px;
}

.home-header__nav .nav__hamberger .nav__hamberger--line:nth-child(3) {
    top: 16px;
}

