.home .home__details {
    height: 100%;
    display: flex;
    box-sizing: border-box;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}
.home .home__details .home-details__text {
    color: white;
    font-size: 3rem;
    font-weight: 600;
    pointer-events: none;

    /* animation: name duration timing-function delay iteration-count direction fill-mode; */
    -webkit-animation: slideInFromLeft 1s ease-out 0s 1;
            animation: slideInFromLeft 1s ease-out 0s 1;
}


.home .home__details .home-details__button {
    color: white;
    padding: 20px;
    border: 2px solid white;
    font-size: 1.5rem;
    cursor: pointer;
    margin-top: 30px;
    position: relative;
    font-weight: 600;
    overflow: hidden;
    transition: .5s;
}

.home .home__details .home-details__button:before {
    content: '';
    width: 100%;
    height: 100%;
    background: #1C818A;
    position: absolute;
    top: 0;
    left: 0;
    z-index: -1;
    -webkit-transform-origin: left bottom;
            transform-origin: left bottom;
    -webkit-transform: rotate(-90deg);
            transform: rotate(-90deg);
    transition: all .5s;
}

.home .home__details .home-details__button:hover:before {
    -webkit-transform: rotate(0);
            transform: rotate(0);

}

.home-details__text--underline {
    position: relative;
}

