}

.home-details__arrow {
    color: white;
    font-size: 2rem;
    position: absolute;
    bottom: 10px;
    
}

.bounce {
    -webkit-animation: bounce 2s infinite;
            animation: bounce 2s infinite;
}

@-webkit-keyframes bounce {
    0%, 20%, 50%, 80%, 100% {
        -webkit-transform: translateY(0);
                transform: translateY(0);
    }
    40% {
        -webkit-transform: translateY(-30px);
                transform: translateY(-30px);
    }
    60% {
        -webkit-transform: translateY(-15px);
                transform: translateY(-15px);
    }
}

@keyframes bounce {
    0%, 20%, 50%, 80%, 100% {
        -webkit-transform: translateY(0);
                transform: translateY(0);
    }
    40% {
        -webkit-transform: translateY(-30px);
                transform: translateY(-30px);
    }
    60% {
        -webkit-transform: translateY(-15px);
                transform: translateY(-15px);
    }
}

@-webkit-keyframes slideInFromLeft {
    0% {
        -webkit-transform: translateX(-100%);
                transform: translateX(-100%);
        opacity: 0.3;
    }
    100% {
        -webkit-transform: translateX(0);
                transform: translateX(0);
        opacity: 1;
    }
}

@keyframes slideInFromLeft {
    0% {
        -webkit-transform: translateX(-100%);
                transform: translateX(-100%);
        opacity: 0.3;
    }
    100% {
        -webkit-transform: translateX(0);
                transform: translateX(0);
        opacity: 1;
    }
}

@-webkit-keyframes slideInFromBottom {
    0% {
        -webkit-transform: translateY(100%);
                transform: translateY(100%);
    }
    100% {
        -webkit-transform: translateY(0);
                transform: translateY(0);
    }
}

@keyframes slideInFromBottom {
    0% {
        -webkit-transform: translateY(100%);
                transform: translateY(100%);
    }
    100% {
        -webkit-transform: translateY(0);
                transform: translateY(0);
    }
}
.portfolio {
    background-color: #F5F5F5;
    height: 100%;
    padding: 100px 0 130px 0
}

.portfolio__projects {
    width: 100%;
    max-width: 1280px;
    display: flex;
    flex-wrap: wrap;
    margin: 0 auto;
    margin-top: 30px;
}

.portfolio__project {
    position: relative;
    width: 300px;
    height: 300px;
    overflow: hidden;
    margin: 10px;
}

.portfolio__project:hover img {
    -webkit-transform: scale(1.2);
            transform: scale(1.2)
}

.portfolio__project .project__details {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    position: absolute;
    background: rgba(0,0,0,.5);
    top: 15px;
    left: 15px;
    bottom: 15px;
    right: 15px;
    -webkit-transform: scaleY(0);
            transform: scaleY(0);
    transition: -webkit-transform 0.5s;
    transition: transform 0.5s;
    transition: transform 0.5s, -webkit-transform 0.5s
}

.portfolio__project:hover .project__details {
    -webkit-transform: scaleY(1);
            transform: scaleY(1)
}

.portfolio__project .project__details h2 {
    color: #fff;
    font-size: 25px;
    cursor: default;
    margin-bottom: 5px;
}

.portfolio__project .project__details p {
    color: #fff;
    margin-bottom: 18px;
    font-size: 20px;
    cursor: default;
}
.portfolio__project .project__details .project-detail__more {
    border: 2px solid tomato;
    padding: 10px;
    cursor: pointer;
    color: white;
    transition: background .5s;
}

.portfolio__project .project__details .project-detail__more:hover {
    border: 2px solid tomato;
    background: tomato;    
}

.portfolio__project img {
    width: 100%;
    height: 100%;
    transition: -webkit-transform 0.5s;
    transition: transform 0.5s;
    transition: transform 0.5s, -webkit-transform 0.5s
}

.contact {
    /* height: 100vh; */
    background: #252934;
    padding: 100px 0 130px 0;
    box-sizing: border-box;
    position: relative;
}

.contact-form__container {
    background: rgba(0,0,0,.3);
    width: 100%;
    height: 460px;
    max-width: 700px;
    margin: 0 auto;
    padding: 40px;
    box-sizing: border-box;
    box-shadow: 0 15px 25px rgba(0,0,0, .5);
    margin-top: 50px;
}

.contact-form__container form {
    display: flex;
    height: 100%;
}

.contact-form__container .contact-form__left {
    flex: 1.2 1;
    padding: 0 30px 0 10px;
    border-right: 1px solid #fff;
    height: 100%;
}

.contact-form__left .contact-form__box:nth-child(1) {
    margin-bottom: 30px;
}

.contact-form__container .contact-form__right {
    flex: 2 1;
    padding: 0 10px;
    position: relative;

}

.contact-form__box {
    position: relative;

}

.contact-form__box label {
    position: absolute;
    z-index: 1;
    top: 0;
    left: 0;
    padding: 7px 7px;
    font-size: 16px;
    pointer-events: none;
    color: #fff;
    font-weight: bold;
    transition: all .5s;
}

.contact-form__box input {
    width: 100%;
    padding: 7px 7px;
    box-sizing: border-box;
    color: #fff;
    font-size: 16px;
    border: none;
    outline: none;
    background: transparent;
    border-bottom: 1.5px solid #fff;
}

.contact-form__box textarea:valid ~ label,
.contact-form__box textarea:focus ~ label,
.contact-form__box input:valid ~ label,
.contact-form__box input:focus ~ label {
    color: #04C2C9;
    top: -20px;
    left: -7px;
    font-size: 13px;
}

.contact-form__box textarea {
    resize: none;
    width: 100%;
    box-sizing: border-box;
    height: 200px;
    border: none;
    outline: none;
    background: transparent;
    color: #fff;
    padding: 7px 7px;
    font-size: 16px;
}

.form-box__submit {
    position: absolute;
    bottom: 0;
    width: 100%;
    border: 1px solid #04C2C9;
}


.form-box__submit input[type="submit"] {
    width: 100%;
    background: transparent;
    color: #04C2C9;
    padding: 10px;
    cursor: pointer;
    border: none;
    transition: .5s;
    text-transform: uppercase;
    font-weight: 600;
}

.form-box__submit:hover input[type="submit"] {
    background: #04C2C9;
    color: white;
}

